import Vue from 'vue';
import Router from 'vue-router';
import Home from './views/Home.vue';
import Gallery from './views/Gallery.vue';
import Videos from './views/Videos.vue';
import Contact from './views/Contact.vue';
import About from './views/About.vue';
import Addupdate from './views/Addupdate.vue';

Vue.use(Router);

export default new Router({
  mode: 'history',
  base: process.env.BASE_URL,
  routes: [
    {
      path: '/',
      name: 'home',
      component: Home,
    },
    {
      path: '/gallery',
      name: 'gallery',
      component: Gallery
    },
    {
      path: '/videos',
      name: 'videos',
      component: Videos
    },
    {
      path: '/contact',
      name: 'contact',
      component: Contact
    },
    {
      path: '/about',
      name: 'about',
      component: About
    },
    {
      path: '/addupdate',
      name: 'addupdate',
      component: Addupdate
    }
  ],
});
