Vue.filter('sectionate', (val:string, len:number = 300) => {
  console.log(val.length);
  
  let tempLen:number = val.length;
  let tempArr:Array<string> = [];

  let beginNum:number = 0;
  let valSend:string = "";

  while(tempLen >= len) {
    console.log("Begin Number: " + beginNum);
    let pointEnd = val.indexOf('.',beginNum+len);
    console.log("Point End: " + pointEnd);
    valSend = val.slice(beginNum, pointEnd+1);
    tempArr.push(valSend);

    let whatTOSub = pointEnd - beginNum;
    console.log("Subtraction: " + whatTOSub);
    
    tempLen -= whatTOSub;
    beginNum = (pointEnd + 1);
    console.log("Temp Length: " + tempLen);
  }
  if(tempLen > 0) 
    tempArr.push(val.slice(val.indexOf(' ', beginNum)+1, val.length))
  console.log(tempArr);
  
  let newString:string = "";
  tempArr.map((v) => {
    newString += v + "<br />";
  });
  console.log(newString);
  return newString;
});

Vue.mixin({
  mounted() {
    
  },
  methods: {
    divStr(val:string, len:number = 300, looping:number = 1) {
      // console.log(val.length);
  
      let tempLen:number = val.length;
      let tempArr:Array<string> = [];
      let beginNum:number = 0;
      let valSend:string = "";

      while(tempLen >= len) {
        // console.log("Begin Number: " + beginNum);
        let pointEnd = val.indexOf('.',beginNum+len);
        // console.log("Point End: " + pointEnd);
        valSend = val.slice(beginNum, pointEnd+1);
        tempArr.push(valSend);

        let whatTOSub = pointEnd - beginNum;
        // console.log("Subtraction: " + whatTOSub);
        tempLen -= whatTOSub;
        beginNum = (pointEnd + 1);
        // console.log("Temp Length: " + tempLen);
      }
      if(tempLen > 0) 
        tempArr.push(val.slice(val.indexOf(' ', beginNum)+1, val.length))
      // console.log(tempArr);
      
      let newString:string = "";
      tempArr.map((v) => {
        let spacing = "";
        for(let i=0; i<looping; i++) {
          spacing += "<br />"
        }
        newString += `<p>${ v }</p>` + spacing;
      });
      // console.log(newString);
      return newString;
    },
    chkScrollbar() {
      // console.log("APP initiated");
      // this.resizeTable();
      // window.addEventListener('resize', this.resizeTable);
      let bodyEl = document.querySelector('body');
      let scrollHeight = bodyEl != null ? bodyEl.scrollHeight : 0;
      let scrollTop = bodyEl != null ? bodyEl.scrollTop : 0;
      let height = bodyEl != null ? bodyEl.clientHeight : 0;
      let selScroller:HTMLElement | null = bodyEl != null ? bodyEl.querySelector(".pf-scroller") : null;
      let realScrollTop = (scrollTop / (scrollHeight-height)) * 100;
      realScrollTop = ( scrollTop / ( scrollHeight- height)) *( scrollHeight- height) ;
      
      /* A D D    S C R O L L   E V E N T   */
      bodyEl != null ? bodyEl.removeEventListener("scroll", bodyElScrollMethod):null;
      bodyEl != null ? bodyEl.addEventListener("scroll", bodyElScrollMethod):null;

      // bodyEl !=null ? bodyEl.scrollTo(0, 600) : null;
      /* ****************************************** */
      let timeScrollVis;
      function bodyElScrollMethod(e:Event) {
        // console.log("scroll");
        bodyEl = document.querySelector('body');
        bodyEl != null ? console.log(bodyEl.scrollHeight) : 0;
        selScroller = bodyEl != null ? bodyEl.querySelector(".pf-scroller") : null;
        scrollHeight = bodyEl != null ? bodyEl.scrollHeight : 0;
        height = bodyEl != null ? bodyEl.clientHeight : 0;
        scrollTop = bodyEl != null ? bodyEl.scrollTop : 0;
        let heightNegate = (height / scrollHeight) * height;
        heightNegate = heightNegate < 0 ? -heightNegate : heightNegate;

        realScrollTop = (scrollTop / (scrollHeight-height)) * ((height - heightNegate));
  
        if(selScroller) {
          selScroller.style.top = realScrollTop + 'px';
          selScroller.style.height = heightNegate + 'px';
          selScroller.style.opacity = "1";
        }
        
        clearTimeout(timeScrollVis);
        timeScrollVis = setTimeout(() => {
          selScroller != null ? selScroller.style.opacity = "0" : null;
          // console.log("clearTimeout");
        }, 2000);

        if(scrollHeight == height && selScroller) 
          selScroller.style.opacity = "0";
        
      }

      window.removeEventListener('resize', bodyElScrollMethod);
      window.addEventListener('resize', bodyElScrollMethod);
      function windowScrollMethod() {
        scrollHeight = bodyEl != null ? bodyEl.scrollHeight : 0;
        height = bodyEl != null ? bodyEl.clientHeight : 0;
        let heightNegate = (height / scrollHeight) * 100;
        heightNegate = heightNegate < 0 ? -heightNegate : heightNegate;
        selScroller = bodyEl != null ? bodyEl.querySelector(".pf-scroller") : null;
        // @ts-ignore
        selScroller.style.height = heightNegate + '%';
      }

      /* A T T A C H    S C R O L L E R    */
      
      /* C R E A T E    S C R O L L E R    */
      let el = document.createElement('div');
      el.setAttribute('class', "pf-scroller");
      el.style.position = 'fixed'; 
      el.style.top = realScrollTop + 'px';
      el.style.right = '3px';
      el.style.width = '5px';
      el.style.transition = "opacity 0.2s cubic-bezier(0.46,-0.01, 0, 1.08)";
      // let heightNegate = height - (scrollHeight - height);
      let heightNegate = (height / scrollHeight) * 100;
      heightNegate = heightNegate < 0 ? -heightNegate : heightNegate;
      el.style.height = heightNegate + '%';
      el.style.borderRadius = '8px';
      el.style.backgroundColor = '#83818488';
      el.style.opacity = "0";
      // console.log('entered Somee');
      if(selScroller) {
        selScroller.remove();
      }
      // @ts-ignore
      bodyEl.insertAdjacentElement('beforeend', el);
      // console.log(el);
      
      // console.log(scrollHeight);
      // console.log(height);
      if(scrollHeight != height) {
        el.style.opacity = "1";
        el.style.pointerEvents = "initial";
      }else if(selScroller) {
        // selScroller.remove();
        // console.log('N O T entered Somee');  
        el.style.opacity = "0";
      }/* END - A T T A C H    S C R O L L E R    */
    },
    nextFocus(e:Event) {
      let el = e.target as HTMLElement;
      let elNext = el.nextElementSibling as HTMLElement | null;
      elNext && elNext.focus();
    }
  },
});

