Vue.directive('tabler', (el, binding, vnode) => {
  setTimeout(tablerMeth, 0);

  function tablerMeth() {
    let getClassname = el.getAttribute('class');
    if(getClassname && getClassname.search("tabler") < 0)
      el.setAttribute("class", getClassname + " tabler");
    
    let counter = 0;

    let observer = new MutationObserver(function (mutations) {
      // el.parentElement ? el.parentElement.
      // console.log(el.parentElement && el.parentElement.clientHeight);
      console.log(mutations);
    });
    observer.observe(el, {
      attributes: true,
      childList: true,
      subtree: true,
    });
    // observer.disconnect();
    
  
    
    window.addEventListener('resize', (e) => {
      // console.log(el);
      calTablerHeight();
    });

    /* _________    FUNCTION    ______________ */
    function calTablerHeight():number {
      let temp:NodeListOf<HTMLElement> = el.querySelectorAll('[t-h]');
      console.log(temp);
      let sizeSub:number = 0;
      let determinedSize:number = 0;
      let valTOSend: number = 0;

      /*  C A L C U L A T E   S I Z E   T O   S U B T R A C T    */
      
      temp.forEach((el2, i, arr) => {
        let tempNodeVal:string | null = null;
        /* S E T    t-h    */
        for(let i=0; i<el2.attributes.length; i++) {
          if(el2.attributes[i].nodeName == "t-h")
          tempNodeVal = el2.attributes[i].nodeValue;
        }
        if(tempNodeVal == "auto") {
          let curHeight = el2.firstElementChild ? el2.firstElementChild.clientHeight : 0;
          sizeSub += curHeight;
        }
        valTOSend = el2.firstElementChild ? el2.firstElementChild.clientHeight : 0;
        console.log(sizeSub);
      });/* ***************************** */


      if(el.parentElement && el.parentElement.clientHeight > 500) {
        determinedSize = el.parentElement.clientHeight - sizeSub;
        
        temp.forEach((el, i, arr) => {
          let tempNodeVal;
          
          /* S E T    t-h    */
          for(let i=0; i<el.attributes.length; i++) {
            if(el.attributes[i].nodeName == "t-h")
              tempNodeVal = el.attributes[i].nodeValue;
          }

          if(tempNodeVal == "*") {
            let elTarget:any = el.firstElementChild;
            elTarget != null ? elTarget.style.height = determinedSize + "px" : null;
          }
        });
      }else if(el.parentElement) {
        temp.forEach((el, i, arr) => {
          let tempNodeVal;
          
          /* S E T    t-h    */
          for(let i=0; i<el.attributes.length; i++) {
            if(el.attributes[i].nodeName == "t-h")
              tempNodeVal = el.attributes[i].nodeValue;
          }

          if(tempNodeVal == "*") {
            let elTarget = el.firstElementChild as HTMLElement | null;
            elTarget != null ? elTarget.style.height = "auto" : null;
          }
        });
        // determinedSize = el.parentElement.clientHeight - sizeSub;
      }
      return valTOSend;
    }/* _____________   END-FUNCTION    ______________ */

    setTimeout(function tempRun() {
      if(calTablerHeight() == 0 && counter < 30) {
        setTimeout(() => {
          counter ++
          tempRun();
        }, 10);
      }
    }, 0);
  }

});

Vue.directive('tbcolumn', (el, binding, vnode) => {
  setTimeout(tableCalcus, 0);
  
  function tableCalcus() {
    let curClassname = el.getAttribute('class');
    if(curClassname && curClassname.search(binding.name) < 0) el.setAttribute('class', curClassname + ' ' + binding.name);
    else el.setAttribute('class', binding.name);
    
    let allCols = el.children;
    let numCols = null;
    numCols = "";

    if(allCols) {
      let getHei = binding.value ? binding.value.height : null;
      
      if(getHei) el.style.minHeight = getHei;
      else el.style.minHeight = "100vh";
    }

    /* C H E C K    I F   THCOL   E X I S T */
    for(let i=0; i<allCols.length; i++){
      let curCol = allCols[i] as HTMLElement;
      let curColClass = curCol.getAttribute('class');
      // let allAttr = curCol.attributes;
      let attr = curCol.attributes.getNamedItem('thcol');
      
      if(attr && attr.value != "*" && typeof attr.value == 'string') {
        
        if(!isNaN(+attr.value)) curCol.style.flexBasis = `${ attr.value }px`;


        if (curColClass && curColClass.search("thcol") < 0) curCol.setAttribute('class', curColClass + " thcol");
        else curCol.setAttribute('class', "thcol")
        
      }else if(attr && attr.value == "*") {
        if (curColClass && curColClass.search("thcol-auto") < 0) curCol.setAttribute('class', curColClass + " thcol-auto");
        else curCol.setAttribute('class', "thcol-auto")
      }
    }/* END   C H E C K    I F   THCOL   E X I S T------ */
    
  }
});

Vue.directive('scroller', (el, binding, vnode) => {
  setTimeout(scrollerMeth, 0);
  // scrollerMeth();
  
  function scrollerMeth() {
    let classname = el.getAttribute('class');
    if(classname != null && classname.search(new RegExp(binding.name)) == -1) {
      el.setAttribute("class", `${ classname } ${ binding.name }`);
    }else if(classname == null) {
      el.setAttribute("class", `${ binding.name }`);
    }

    el.style.overflow = "auto";
    
    let val = binding.value;
    if(val) {
      // el.style.height = binding.arg..height;
      val.height != undefined ? el.style.height = val.height : null;
    }
    
    let secShow = 600;
    
    let scrollHeight = el.scrollHeight;
    let scrollTop = el.scrollTop;
    let height = el.clientHeight;
    let classnameScollbar = "pf-scroller";
    // let selScroller:HTMLElement | null = el.querySelector(`.${ classnameScollbar }`);
    let selScroller:HTMLElement | null = erHTMLChild(el.children, classnameScollbar);
    
    let heightNegate = (height / scrollHeight) * height;
    heightNegate = heightNegate < 0 ? -heightNegate : heightNegate;

    let realScrollTop = (scrollTop / (scrollHeight-height)) * 100;
    realScrollTop = ((scrollTop / (scrollHeight-height)) * (height - heightNegate)) + scrollTop;
    if(isNaN(realScrollTop)) realScrollTop = 0;    
    
    /* A D D    S C R O L L   E V E N T   */
    el.removeEventListener("scroll", elScrollMethod);
    el.addEventListener("scroll", elScrollMethod);
  
    // el.scrollTo(0, 600);
    /* ****************************************** */
    let timeScrollVis;
    function elScrollMethod(e:Event) {
      // console.log("scroller");
      selScroller = erHTMLChild(el.children, classnameScollbar);
      scrollHeight = el.scrollHeight;
      height = el.clientHeight;
      scrollTop = el.scrollTop;
      heightNegate = (height / scrollHeight) * height;
      heightNegate = heightNegate < 0 ? -heightNegate : heightNegate;
      
      realScrollTop = ((scrollTop / (scrollHeight-height)) * (height - heightNegate)) + scrollTop;
      // console.log(scrollHeight, realScrollTop);

      // console.log(scrollHeight, height);
      if(scrollHeight == height) {
        return selScroller != null ? selScroller.style.display = 'none' : null;
      }else {
        selScroller != null ? selScroller.style.display = 'initial' : null;
      }
  
      selScroller !=null ? selScroller.style.top = realScrollTop + 'px' : null;
      selScroller !=null ? selScroller.style.height = heightNegate + 'px' : null;
      selScroller != null ? selScroller.style.opacity = "1" : null;
      selScroller != null ? selScroller.style.pointerEvents = "initial" : null;
      
      clearTimeout(timeScrollVis);
      timeScrollVis = setTimeout(() => {
        selScroller != null ? selScroller.style.opacity = "0.1" : null;
        // console.log("clearTimeout");
      }, secShow);
      setTimeout(() => {
      
      }, 0);
      
    }
    function elScrollMethods(this:GlobalEventHandlers, ev:UIEvent) {
      console.log("scroller");
      selScroller = erHTMLChild(el.children, classnameScollbar)
      scrollHeight = el.scrollHeight;
      height = el.clientHeight;
      scrollTop = el.scrollTop;
      heightNegate = (height / scrollHeight) * height;
      heightNegate = heightNegate < 0 ? -heightNegate : heightNegate;
  
      realScrollTop = ((scrollTop / (scrollHeight-height)) * (height - heightNegate)) + scrollTop;
  
      selScroller !=null ? selScroller.style.top = realScrollTop + 'px' : null;
      // @ts-ignore
      selScroller !=null ? selScroller.style.height = heightNegate + 'px' : null;
  
      selScroller != null ? selScroller.style.opacity = "1" : null;
      
      clearTimeout(timeScrollVis);
      timeScrollVis = setTimeout(() => {
        selScroller != null ? selScroller.style.opacity = "0" : null;
        console.log("clearTimeout");
      }, secShow);
      
    }
  
    el.removeEventListener('resize', elScrollMethod);
    window.addEventListener('resize', elScrollMethod);
  
    /* A T T A C H    S C R O L L E R    */
    
    let elscroller = document.createElement('div');
    elscroller.setAttribute('class', classnameScollbar);
    elscroller.style.position = 'absolute'; 
    elscroller.style.top = realScrollTop + 'px';
    elscroller.style.right = '3px';
    // elscroller.style.cursor = 'pointer';
    
    // elscroller.style.width = '5px';
    elscroller.style.paddingLeft = '8px';
    elscroller.style.paddingRight = '1px';
    elscroller.style.transition = "opacity 0.2s cubic-bezier(0.46,-0.01, 0, 1.08)";
    // let heightNegate = height - (scrollHeight - height);
    // let heightNegate = (height / scrollHeight) * 100;
    // heightNegate = heightNegate < 0 ? -heightNegate : heightNegate;
    elscroller.style.height = heightNegate + 'px';

    let elscrollerInner = document.createElement('div');
    elscrollerInner.style.borderRadius = '8px';
    elscrollerInner.style.position = "relative";
    elscrollerInner.style.width = "5px";
    elscrollerInner.style.height = "100%";
    elscrollerInner.style.backgroundColor = '#83818488';

    elscroller.insertAdjacentElement("beforeend", elscrollerInner)

    let mouseTiming = 0;
    let mousestilldown = false;
    let dis = {
      start: 0,
      end: 0,
      mea: 0,
      top: 0,
      duration: 0
    }
    
    elscroller.addEventListener('mouseover', (e) => {
      clearTimeout(timeScrollVis);
      elscroller.style.opacity = "1";
      
    });
    elscroller.addEventListener('mouseout', (e) => {
      timeScrollVis = setTimeout(() => {
        elscroller.style.opacity = "0.1";
      }, secShow);
    });
    /* ---------------------------------------- */
    elscroller.addEventListener('mousedown', (e) => {
      e.preventDefault();
      dis.start = e.clientY;
      dis.top = el.scrollTop;
      dis.duration = 0;
      mouseTiming = setInterval(() => {
        elscroller.style.opacity = "1";
        dis.duration ++
        mousestilldown = true;
        // console.log(dis.duration);
      }, 1);

    });
    window.addEventListener('mousemove', (e) => {
      
      if(dis.duration > 0 && mousestilldown) {
        // console.log(e);
        dis.end = e.clientY;
        dis.mea = dis.end - dis.start;

        el.scrollTop = dis.top + (dis.mea * ((el.scrollHeight/el.clientHeight) * 0.95));
      }
    });
    window.addEventListener('mouseup', (e) => {
      if(dis.duration && mousestilldown) {
        clearInterval(mouseTiming);
        // console.log(dis.duration);
        dis.duration = 0;
        mousestilldown = false;
        // console.log(dis);
      }
    });
    
    if(selScroller) {
      selScroller.remove();
    }
    // @ts-ignore
    el.insertAdjacentElement('beforeend', elscroller);

    if(scrollHeight != height) {
      elscroller.style.opacity = "1";
      
      setTimeout(() => {
        elscroller.style.opacity = "0.1";
      }, secShow);
      elscroller.style.pointerEvents = "initial";
    }else {
      elscroller.style.opacity = "0";
      elscroller.style.pointerEvents = "none";
      // selScroller.remove();
    }/* END - A T T A C H    S C R O L L E R    */
  }
});

Vue.directive('insertViewer', (el, binding, vnode) => {
  setTimeout(insViewer, 0);
  
  function insViewer() {
    console.log("insertViewer");
    let insertViewerDiv = document.createElement('div');
    insertViewerDiv.setAttribute('class', binding.name);
    
    el.insertAdjacentElement('afterend', insertViewerDiv);

    // C R E A T   &    I N S E R T   S T Y L E
    let styling = document.createElement('style');
    let head = document.getElementsByTagName('head');

    let idName = `sty-${ binding.name }`;
    styling.setAttribute('type', 'text/css');
    styling.setAttribute('id', idName);
    let stylingStr = `
      .insert-viewer {
        position: absolute;
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
        background-color: #485a7363;
        background-image: url("./img/bg_pf-100.jpg");
      }
      `;
    styling.insertAdjacentText('beforeend', stylingStr.replace(/^[\s]+/gim, ""));
    if(document.getElementById(idName) == null)
      head[0].insertAdjacentElement('beforeend', styling);
    console.log(document.getElementById(idName));
  }
});

