









































export default {
mounted() {
  if(this.$route.name != 'home') {
    this.showNavLogo = true;
    this.$refs.pfHeader.style.backgroundColor = "#1A161C";
  }else {
    this.showNavLogo = false;
  }

  chkShowNav(this);
  window.addEventListener("resize", (e) => {
    chkShowNav(this);
    
  });

  function chkShowNav(self) {
    let width = window.innerWidth;
    let height = window.innerHeight;
    if(width <= 600) {
      if(self.$route.name != 'home') self.showNav = false;
      self.showMenu = true;
    }else {
      self.showNav = true;
      self.showMenu = false;
    }
  }
},
data: () => ({
  navListing: [{
    path: '/',
    routename: 'home',
    title: 'Home',
    icon: 'house'
  },{
    path: '/gallery',
    routename: 'gallery',
    title: 'Gallery',
    icon: 'camera-3'
  },{
    path: '/videos',
    routename: 'videos',
    title: 'Videos',
    icon: 'youtube-play-outline'
  },{
    path: '/contact',
    routename: 'contact',
    title: 'Contact',
    icon: 'device-mobile'
  },{
    path: '/about',
    routename: 'about',
    title: 'About',
    icon: 'globe-world'
  }],
  showNavLogo: false,
  showNav: true,
  showMenu: true,
  menuActive: false
}),
methods: {
  menuClk(e) {
    this.menuActive = !this.menuActive;
    console.log(this.menuActive);
  }
}
}
