export default {
  name: 'App',
  mounted() {
    
  },
  data () {
    return {
      //
    }
  },
  methods: {
    resizeTable() {
      if(this.$route.name == "home") {
        
        let header:HTMLElement | null = document.querySelector("#pf-home .layout.header-nav");
        let mid:HTMLElement | null = document.querySelector("#pf-home .layout.mid-title");
        let footer:HTMLElement | null = document.querySelector("#pf-home .layout.footer");
        let windowHeight = window.innerHeight;
        let availableHeight = windowHeight - ((header != null ? header.clientHeight : 0) + (footer != null ? footer.clientHeight: 0))
  
  
        if(windowHeight >= 500) 
          mid != null ? mid.style.height = availableHeight + "px" : null;
      }


    }
  },
}
