


































































import { mapGetters, mapActions } from 'vuex';

export default {
mounted() {

  this.setGallery().then(() => {

    if(this.getGallery) {
      let received = Object.entries(this.getGallery);
      received.forEach(v => {
        let vv = v[1] as {}[];
        if(vv.length > 0) { v.push(true);
        }else { v.push(false);
        }
      });
      
      this.categoryItems = received;
      this.loading = false;
    }
  });
},
data: () => ({
  categoryItems: <any> null,
  loading: true,
  dataSend: null
}),
computed: {
  ...mapGetters(['getGallery'])
},
methods: {
  ...mapActions(['setGallery']),
  resizeProportion(e:Event) {
    // console.log(e);
  },
  imgClick(e:Event, item:any) {
    if(item != null) {
      if(item.link) {
        item = {
          ...item,
          curDate: new Date().getTime()
        }
        this.dataSend = item;

      }
    }else {
      console.log("Item is empty!");
    }
  },
  imgClickIden(e:Event, item:Object) {
    console.log(item);
  }
},

}
