data: () => ({
  eAnalyzer: {
    start: { x: 0, y: 0 },
    end: { x: 0, y: 0 },
    distance: { x: 0, y: 0 },
    duration: 0,
    x: 0,
    y: 0,
    prevX: 0,
    prevY: 0,
    started: false,
    tranStarted: false,
    timeOut: 0
  },
  changeImgHeight: false,
  currentImg: 0,
  dataReceived: null,
  arrayToShow: null,
  chkDataR: false
}),
computed: {
  iloTurnOn() {
    let ans = false;
    if(this.chkDataR) {
      this.dataReceived = this.dataR;
      ans = true;
    }else {
      this.dataReceived = null;
      ans = false;
    }

    return ans;
  }
},
methods: {
  startE(e:Event) {
    e.stopPropagation();
    // if the mouseup event fired correctly***
    if(this.eAnalyzer.started != false)  return;

    let imgInView: NodeListOf<HTMLElement> = document.querySelectorAll("[ilo-img]");
    let currentImg = imgInView[this.currentImg];
    currentImg = currentImg.parentElement as HTMLElement;

    // console.log("Top");
    
    this.eAnalyzer.prevX = +currentImg.style.left.replace("px", "");
    this.eAnalyzer.prevY = +currentImg.style.top.replace("px", "");
    this.eAnalyzer.distance.x = 0; this.eAnalyzer.distance.y = 0;
    this.eAnalyzer.duration = 0;
    this.eAnalyzer.end.x = 0; this.eAnalyzer.end.y = 0;
    if(e.type == "mousedown") {
      let ee:MouseEvent = e as MouseEvent;
      // console.log(e);
      this.eAnalyzer.start.x = ee.screenX;
      this.eAnalyzer.start.y = ee.screenY;
    }else if(e.type == "touchstart") {
      let ee:TouchEvent = e as TouchEvent;
      // console.log(e);
      this.eAnalyzer.start.x = ee.touches[0].screenX;
      this.eAnalyzer.start.y = ee.touches[0].screenY;

    }

    //  CHECK IF SWIPE LEFT-RIGHT OR UP-DOWN...
    setTimeout(() => {
      let x = this.eAnalyzer.distance.x, y = this.eAnalyzer.distance.y;
      x = x < 0 ? -x : x;
      y = y < 0 ? -y : y;

      // if(x >= y) console.log("Left-Right",this.eAnalyzer.distance.x, this.eAnalyzer.distance.y);
      // else console.log("Up-Down", this.eAnalyzer.distance.x, this.eAnalyzer.distance.y);
    }, 100);

    
    let self = this;
    (function loop() {
      self.eAnalyzer.timeOut = setTimeout(() => {
        self.eAnalyzer.duration += 0.5;
        // console.log("Timing...");
        loop();
      },5);
    })();

    this.eAnalyzer.started = true;
    this.eAnalyzer.tranStarted = false;

  },
  movingE(e:Event) {
    e.stopPropagation();
    if(this.eAnalyzer.started != true) return;
    if(e.type == "mousemove") {
      let ee:MouseEvent = e as MouseEvent;
      // console.log(e.pageX);
      this.eAnalyzer.end.x = ee.screenX;
      this.eAnalyzer.end.y = ee.screenY;
    }else if(e.type == "touchmove") {
      let ee:TouchEvent = e as TouchEvent;
      // console.log(e.touches[0].screenX);
      this.eAnalyzer.end.x = ee.touches[0].screenX;
      this.eAnalyzer.end.y = ee.touches[0].screenY;
    }
    
    (function movingtheSame(self:any,ea:any) {
      ea.distance.x = ea.end.x - ea.start.x;
      ea.distance.y = ea.end.y - ea.start.y;

      // console.log(self.eAnalyzer.distance.x, self.eAnalyzer.distance.y);

      let imgsInView = document.querySelectorAll("[ilo-img]") as NodeListOf<HTMLElement>;
      for (let i = 0; i < imgsInView.length; i++) {
        let img = imgsInView[i];
        img = img.parentElement as HTMLElement;

        if(i == self.currentImg) {
          img.style.top = ((ea.prevY + ea.distance.y) + "px");
          img.style.left = ((ea.prevX + ea.distance.x )+ "px");
        }else {
          img.style.left = ((ea.prevX + ea.distance.x )+ "px");

        }
      }
    })(this, this.eAnalyzer);
  },
  endE(e:Event) {
    e.stopPropagation();
    // if the mousedown is properly STARTED***
    if(this.eAnalyzer.started != true) return;
    let x = this.eAnalyzer.distance.x;
    let y = this.eAnalyzer.distance.y;

    this.eAnalyzer.started = false;
    this.eAnalyzer.tranStarted = true;
    setTimeout(() => { this.eAnalyzer.tranStarted = false; }, 500);
    clearTimeout(this.eAnalyzer.timeOut);
    if(e.type == "mouseup") {
      // console.log(e);
    }else if(e.type == "touchend") {
      // console.log(e);
    }
    
    (function endMeth(self:any, ea:any) {
      let iloParent = document.querySelector("[ilo-parent]") as HTMLElement;
      let imgsInView = document.querySelectorAll("[ilo-img]") as NodeListOf<HTMLElement>;
      let parentWidth = iloParent.clientWidth;
      let threshold = 100;

      // SELECT PREVIOUS
      if(x > threshold) {
        if(self.currentImg >= imgsInView.length-1) {
          self.currentImg --;
        }else if(self.currentImg <= 0) {
          self.currentImg = imgsInView.length - 1
        }else {
          self.currentImg --;
        }
        

      // SELECT NEXT
      }else if(x < -threshold) {
        if(self.currentImg >= imgsInView.length-1) {
          self.currentImg = 0;
        }else if(self.currentImg <= 0) {
          self.currentImg ++
        }else {
          self.currentImg ++;
        }

      // REVERT TO PREV POSITION
      }else {
      }

      self.setCurrentImage(self);

      // console.log(self.currentImg);

    })(this, this.eAnalyzer);

  },
  leaveE(e:Event) {
    e.stopPropagation();
    // clearTimeout(this.eAnalyzer.timeOut);
    // console.log("Left wae");
  },
  closeClick(e:Event) {
    e.stopPropagation();
    this.chkDataR = false;
  },
  setDataRArray(newVal:[]) {
    //  SET THE ARRAY VALUE...
    if(newVal) {
      let cat = this.dataR != null && this.dataR.category;
      let arrToShow = [];
      newVal.forEach((val:[]) => {
        if(val.join("++").search(cat) > -1) {
          arrToShow = val;
          this.arrayToShow = arrToShow;
        }
      });
      
      console.log(arrToShow);
    }else {
      console.log(newVal);
      console.log(this.dataR);
    }

    //  SET THE INDEX OF THE IMAGE TO SHOW...
    if(this.dataR) {
      let theArr: [] = this.arrayToShow[1];
      theArr.forEach((val:any,i,arr) => {
        if(val.con == this.dataR.con) {
          this.currentImg = i;
          this.setCurrentImage(this);
        }
        
      });
    }

  },
  setCurrentImage(self) {
    setTimeout(() => {
      let iloParent = document.querySelector("[ilo-parent]") as HTMLElement;
      if(iloParent == null) return console.log("iloParent", iloParent);;
      let imgsInView = document.querySelectorAll("[ilo-img]") as NodeListOf<HTMLElement>;
      let parentWidth = iloParent.clientWidth;
      for (let i = 0; i < imgsInView.length; i++) {
        let img = imgsInView[i];
        img = img.parentElement as HTMLElement;
        /* if(i == self.currentImg) {
          let midTop = (iloParent.clientHeight/2) - (img.clientHeight/2);
          let midLeft = (iloParent.clientWidth/2) - (img.clientWidth/2);
          img.style.top = ((0) + "px");
          img.style.left = ((0)+ "px");
        }else {
          img.style.left = ((0)+ "px");
          
        } */
        img.style.top = ((0)+ "px");
        img.style.left = ((-(self.currentImg*parentWidth))+ "px");
        
      }
    }, 0);
  },
  checkWidth() {
    console.log("Check Width");
    let threshold = 0.95;
    let ilo = document.querySelector("[ilo-parent]") as HTMLElement;
    let imgsInView = document.querySelectorAll("[ilo-img]") as NodeListOf<HTMLElement>;
    if(ilo == null) return;
    let mainHeight = ilo.clientHeight;
    let mainWidth = ilo.clientWidth;

    imgsInView.forEach((currentImg,i,arr) => {
      let currentHeight = currentImg.clientHeight;
      let currentWidth = currentImg.clientWidth;
      let imgData = this.arrayToShow[1][i]
      
      if((mainWidth * threshold) <= currentWidth) {
        imgData.toHeight = false;
        console.log("Width: ", (mainWidth * threshold), currentWidth);
      }else if((mainHeight * threshold) < currentHeight) {
        imgData.toHeight = true;
        console.log("Height: ", (mainHeight * threshold), currentWidth);
      }
    });
    this.setCurrentImage(this);
  }
},
watch: {
  dataR(newVal) {
    if(newVal) this.chkDataR = true;
    else this.chkDataR = false;
    this.setDataRArray(this.dataRArray);
  },
  dataRArray(newVal) {
    this.setDataRArray(newVal);
  }
},

