import Vue from 'vue';
import Vuex from 'vuex';

import axios from "axios";

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    gallery: null,
    category: null
  },
  getters: {
    getGallery(state) {
      return state.gallery;
    },
    getCategory(state) {
      return state.category;
    }
  },
  mutations: {
    SETGALLERY(state, payload) {
      state.gallery = payload;
    },
    SETCATEGORY(state, payload) {
      state.category = payload;
    }
  },
  actions: {
    setGallery({commit}, payload) {
      return new Promise((resolve, reject) => {
        let sendData = {
          "outdoor": [{
            "name": "Peacc",
            "path": "",
            "date": 0
          },{
            "name": "Peacc",
            "path": "",
            "date": 0
          },{
            "name": "Peacc",
            "path": "",
            "date": 0
          },{
            "name": "Peacc",
            "path": "",
            "date": 0
          }],
          "studio": [{
            "name": "Hooll",
            "path": "",
            "date": 0
          },{
            "name": "Hooll",
            "path": "",
            "date": 0
          },{
            "name": "Hooll",
            "path": "",
            "date": 0
          }],
          "wedding": [{
            "name": "Secct",
            "path": "",
            "date": 0
          },{
            "name": "Secct",
            "path": "",
            "date": 0
          },{
            "name": "Secct",
            "path": "",
            "date": 0
          },{
            "name": "Secct",
            "path": "",
            "date": 0
          },{
            "name": "Secct",
            "path": "",
            "date": 0
          }]
        }
        axios.get("http://localhost/web-files/proffilms-server/gallery/get").then((res) => {
          
          let catList:string[] = [];
          let obj =  res.data;
          if(typeof obj == 'object') {
            let miscCategoryName = "misc";
            for(let i=0; i<obj.length; i++) {
              let val = obj[i];
              if(val && val.category) {
                if(catList.join("+=").search(val.category) < 0) catList.push(val.category);
              }else if(val && val.category == null) {
                val.category = miscCategoryName;
                if(catList.join("+=").search(miscCategoryName) < 0) catList.push(miscCategoryName);
              }
            }
            // console.log(catList);
            // console.log(obj);
            catList.sort((b, a) => ((a != "misc")? 1: -1));
            
            let dataSender:any = {};
            for(let i = 0; i < catList.length; i++) {
              const category:string = catList[i];
              dataSender[category] = [];
              
              for(let ii = 0; ii < obj.length; ii++) {
                const val = obj[ii];
                val.toHeight = false;
                if(val.category && val.category == category) {
                  dataSender[category].push(val);
                }else if(val.category == null) dataSender[category].push(val);
              }
            }
            console.log(dataSender);
            commit('SETGALLERY', dataSender);
            resolve();

          }else {
            reject("The Returned from the database is Not any Object.");
          }

        }).catch((err) => {
          console.info(err);
          commit('SETGALLERY', sendData);
          reject();
        });
      });
    },
    setCategory({commit}, payload) {
      return new Promise((resolve, reject) => {
        axios.get("http://localhost/web-files/proffilms-server/category/get").then(async (res) => {
          let getObj = res.data as Object;
          
          // console.log(typeof getObj);
          if(typeof getObj == 'object') commit('SETCATEGORY', getObj);
          else console.log("Check The Object coming into Category.");
          resolve();
        }).catch(async (err) => {
          console.info(err);
          reject();
        });
      });
    }
  },
});
