


































































































import axios from "axios";
import { mapActions, mapGetters } from 'vuex';

export default {
mounted() {
  this.setCategory().then(() => {
    
    let returnObj = this.getCategory;
    if(typeof returnObj == 'object') {
      this.selCategory.items = returnObj;
    }
  });

  

  // console.log(singleSets);
},
data: () => ({
  checked: false,
  selCategory: {
    current: <any>null,
    items: [{
      name: "Rate"
    },{
      name: "Pace"
    },{
      name: "Ord"
    }] 
  },
  selDelReverse: {
    current: <any>null,
    items: [{
      name: "Delete",
      value: 0
    },{
      name: "Reverse Delete",
      value: 1
    }] 
  },
  formCenter: {
    name: "",
    filename: "",
    file: <any>null,
    files: <any>null,
    imgSelected: false
  },
  formDelReverse: {
    name: ""
  },
  linkDownload: <any> null

}),
computed: {
  ...mapGetters(['getCategory']),
  informFile() {
    console.log(this.formCenter.file);
  }
},
methods: {
  ...mapActions(['setCategory']),
  getFile(e:Event) {
    let fileTarget = e.target as HTMLInputElement | null;
    let files = fileTarget && fileTarget.files;
    let file = files && files[0];

    console.log(files && Object.entries(files));
    console.log((file instanceof File));

    if(files && file) {
      console.log(this.formCenter.file);
      this.formCenter.file = file;
      this.formCenter.files = files;
      if((file.size / (1024*1024)) < 1){
        console.log((file.size / (1024)).toFixed(0) + " KB");
      }else {
        console.log((file.size / (1024*1024)).toFixed(3) + " MB");
        
      }
      this.formCenter.filename = file.name;
      let lastMod = file.lastModified;
      if(typeof lastMod == 'number') {
        console.log(new Date(lastMod));
      }
      this.formCenter.imgSelected = true;
    }else {
      this.formCenter.imgSelected = false;
    }

  },
  submitForm(e:Event) {

    console.log(this.formCenter);
    let ancReceived = (this.linkDownload as HTMLAnchorElement);
    ancReceived && ancReceived.click();
    let con:Object | null = this.selCategory.current;

    if(this.formCenter.imgSelected) {
      
      let formData = (this.genFormData(this.formCenter.files) as FormData);
      axios.post("http://localhost/web-files/proffilms-server/uploading", formData).then((imgUploaded) => {
        console.log(imgUploaded.data);
      }).catch((err) => {
        console.log(err);
      });
    }

    
  },
  submitDelReverse(e:Event) {
    console.log(this.selDelReverse.current);
    let send = this.selDelReverse.current;
    let name = this.formDelReverse.name;
    axios.get("http://profflims.000webhostapp.com/profflims-server/delReverse", {
        params: {
          permit: "1",
          iden: name,
          date: new Date().getTime()
        }
      }).then((res) => {
      console.log(res);
    });
    return;
    if(send != null && name != null) {
      axios.get("http://localhost/web-files/proffilms-server/delReverse", {
        params: {
          permit: "1",
          iden: name,
          value: send.value,
          date: new Date().getTime()
        }
      }).then((res) => {
        console.log("Response", res);
      });

    }else {
      console.log("Name: ", name);
    }
  },
  openMenu(e:Event) {
    let el = e.target as HTMLElement;
    let elNext = el.nextElementSibling as HTMLElement;
    let selInput = elNext.querySelector('.v-input__slot') as HTMLElement;
    selInput && selInput.click();
    
  },
  createStylesheet() {
    let stylesheet = `&[m-star-o]::before {
      content: 'A';
    }
    &[m-star]::before {
      content: 'B';
    }
    &[m-newtag]::before {
      content: 'C';
    }
    &[m-heart-o]::before {
      content: 'D';
    }
    &[m-like]::before {
      content: 'E';
    }
    &[m-like-2]::before {
      content: 'F';
    }
    &[m-like-3]::before {
      content: 'G';
    }
    &[m-like-4]::before {
      content: 'H';
    }
    &[m-search]::before {
      content: 'I';
    }
    &[m-search-2]::before {
      content: 'J';
    }
    &[m-comment-2]::before {
      content: 'K';
    }
    &[m-comment]::before {
      content: 'L';
    }
    &[m-user]::before {
      content: 'M';
    }
    &[m-on]::before {
      content: 'N';
    }
    &[m-sign-in-3]::before {
      content: 'O';
    }
    &[m-sign-out-3]::before {
      content: 'P';
    }
    &[m-sign-in-2]::before {
      content: 'Q';
    }
    &[m-sign-out-2]::before {
      content: 'R';
    }
    &[m-sign-in]::before {
      content: 'S';
    }
    &[m-sign-out]::before {
      content: 'T';
    }
    &[m-heart]::before {
      content: 'U';
    }
    &[m-applause]::before {
      content: 'V';
    }`;

    stylesheet = stylesheet.replace(/[\r\n\s]+/gim, "");
    let singleSets = [''];
    let tempLeftOver = stylesheet.length, begin = 0, end = 0;

    while(tempLeftOver > 0) {
      end = stylesheet.indexOf("}", begin)+1;
      singleSets.push(stylesheet.slice(begin, end));
      
      if((end - begin) <= 0) tempLeftOver = 0;
      else tempLeftOver -= (end - begin);

      begin = end;
    }

    let devStyleStr = "";
    singleSets.forEach(str => {
      if(str != "") {
        let workOn = str.slice(0, str.indexOf("{"));
        let newStr = "." + workOn.replace(/[\&\[\]]/gim, "");
        let strDivided = str.split(/(\{)/);
        let developedStr = "";
        for(let i=0;i<strDivided.length;i++) {
          let v = strDivided[i];
          if(i==0) developedStr += v + "," + newStr;
          else developedStr += v;
        }

        // console.log(developedStr.replace("&", ".m-icon")+ "\r\n");
        devStyleStr += developedStr.replace("&", ".m-icon")+ "\r\n";
      }
    });

    let formData = new FormData();
    formData.append("devStyle", devStyleStr);
    formData.append("filename", "m-icon-gen-v1.css");

    axios.post("http://localhost/web-files/proffilms-server/devstylesheet", formData).then((res) => {
      let linkDownload = res.data;
      console.log(typeof linkDownload);
      if(typeof linkDownload == 'string') {
        let anc = document.createElement("a");
        anc.setAttribute("href", linkDownload);
        anc.setAttribute("target", "o");
        // anc.setAttribute("download", "pleasedownload");
        this.linkDownload = anc as HTMLAnchorElement;
        
      }

    }).catch((err) => {
      
    });
  },
  genFormData(arr:Object, name:string = "file") {
    // arr = {
    //   le: "leoo",
    //   goe: "ewn"
    // }
    let fData = new FormData();
    let temp = Object.entries(arr);
    console.log(temp);
    let totalSize = 0;
    let err = false;
    if((arr instanceof FileList)){

      temp.forEach(v => {
        if((v[1].size / (1024*1024))>2)
          console.log("Size Greater than 2MB");
        totalSize += v[1].size;
        fData.append(`${name}[${v[0]}]`, v[1]);
      });
    }else {
      temp.forEach(v => { fData.append(v[0], v[1]); });
    }

    console.log((Math.ceil((totalSize / (1024*1024))*1000))/1000 + "MB");
    fData.append("alimake", "");
    fData.append("con", this.selCategory.current.con);
    fData.append("createdat", new Date().getTime().toString());
    return fData;
    
  }

},
watch: {
  
},
}
